.selection {
  margin-top: -90px;
}

.selection_menu {
  text-align: center;
  background: #b80e14;
  margin-bottom: 25px;
  margin-top: -95px;
}

.selection_menu li {
  position: relative;
  transition: all .4s;
}

.selection_menu li a {
  color: #ffffff;
  font-size: 26px;
  position: relative;
  width: 100%;
  padding: 25px 0;
  -webkit-box-shadow: inset 0px -191px 55px -195px rgba(0, 0, 0, 0.46);
  -moz-box-shadow: inset 0px -191px 55px -195px rgba(0, 0, 0, 0.46);
  box-shadow: inset 0px -191px 55px -195px rgba(0, 0, 0, 0.46);
  padding-left: 100px;
  text-align: left;
}

.selection_menu li a:hover {
  box-shadow: none;
}

.selection_menu li .nested a {
  position: relative;
  text-transform: none;
}

.selection_menu li .nested a:before {
  content: '';
  position: absolute;
  left: 80px;
  top: 50%;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-left: 7px solid #ffffff;
  border-bottom: 5px solid transparent;
  transform: translateY(-50%);
}

.selection_menu li .open-menu {
  width: 42px;
  height: 42px;
  padding: 0;
  position: absolute;
  text-align: 0;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  background: url("/images/main/arrow-nav.png") no-repeat;
  background-size: contain;
  cursor: pointer;
}

.selection_menu li .open-mobile-menu {
  width: 16px;
  height: 10px;
  padding: 0;
  position: absolute;
  text-align: 0;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  background: url("/images/main/arrow-nav-mobile.png") no-repeat;
  background-size: contain;
  cursor: pointer;
  box-shadow: none;
}

.selection_menu li .top {
  width: 42px;
  height: 42px;
  background: url("/images/main/arrow-nav-top.png") no-repeat;
  background-size: contain;
}

.selection_menu li .mobile-top {
  width: 16px;
  height: 10px;
  background: url("/images/main/arrow-nav-mobile-top.png") no-repeat;
  background-size: contain;
}

.selection_menu li:hover {
  background: #9b070d;
}

.selection_menu li:hover .square {
  background: #bababa;
}

.selection_menu_contact {
  background: #274293;
}

