  background: url("/images/main/arrow-prev-hover.png") no-repeat center;
}

.pager .previous a:before {
  content: '';
  position: absolute;
  width: 38px;
  height: 38px;
  -webkit-transition: all .4s;
  transition: all .4s;
  background: url(/images/main/arrow-prev.png) no-repeat center;
  border: 1px solid #939393;
  left: 0;
  top: 0;
  border-radius: 100%;
}

.pager .next a {
  position: relative;
  border: none;
  padding-right: 50px;
  color: #000000;
  transition: all .4s;
}

.pager .next a:hover {
  background-color: transparent;
}

.pager .next a:hover:before {
  background-color: #d21017;
  background: url(/images/main/arrow-next-hover.png) no-repeat center;
}

.pager .next a:before {
  content: '';
  position: absolute;
  width: 38px;
  height: 38px;
  -webkit-transition: all .4s;
  transition: all .4s;
  background: url(/images/main/arrow-next.png) no-repeat center;
  border: 1px solid #939393;
  right: 0;
  top: 0;
  border-radius: 100%;
}

.popup {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: none;
  text-align: center;
}

.popup_bg {
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
}

.popup_img {
  position: relative;
  margin: 0 auto;
  z-index: 2;
  max-height: 94%;
  max-width: 94%;
  margin: 1% 0 0 0;
}

