.contact {
    background-color: $back-contact;
    padding: 25px 0;
    
    &_margin {
        margin-bottom: 30px;
    }
    
    .flex-container {
        position: relative;
        &:before { 
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            border-left: 25px solid transparent;
            border-right: 25px solid transparent;
            border-bottom: 25px solid $back-contact;
            top: -50px;
            left: 150px;
        }        
    }
//    Avatar
///////////////////
    &_wrapper-avatar {
        max-width: 130px;
        width: 100%;
        float: left;
    }
    
//list contacts
////////////////////////
    &_list-contact {
        padding-left: 145px;
        
        li {
            color: black;
            font-weight: 700;
            font-size: 17px;
            
            &:first-child {
                text-transform: uppercase;
                font-size: 20px; 
                font-weight: 700;
                color: $white;
            }
        }
        
        &_links {
            padding-left: 0px;
            
            li {
                &:first-child {
                    
                    a {
                        padding-left: 15px;
                        font-size: 15px;
                    }
                }
            }
            a {
                color: $white;
                font-size: 16px;
                position: relative;
                padding-left: 20px;
                font-weight: normal;
                .fa {
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    font-size: 16px;
                }
                
                .fa-mobile {
                    font-size: 25px;
                }
            }
        }
    }
}