.footer {
    background-color: $background-footer;
    padding: 40px 0 15px;
    font-family: 'Arial', sans-serif;
    
    
//    Smile
///////////////////////////
    &_smile {
         color: $white;
        font-size: 12px;
        
        img {
            max-width: 140px;
            margin-bottom: 15px;
        }
        
        &_copy-right {
            color: $copy-right;
            margin-top: 20px;
        }
    }
    
//Offer
    &_offert {
        li {
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
            a {
                color: $color-offer;
                text-decoration: underline;
            }
        }
    }
    
//    Social
    &_left_social {
        max-width: 50%; 
        width: 100%; 
        float: left;        
        
        &_title {
            text-transform: uppercase;
            color: $white;
            margin-bottom: 25px;
        }
        
        &_social {
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            
            li {
                margin-right: 10px;
                &:last-child {
                    margin-right: 0;
                }
                a {
                    width: 30px;
                    height: 30px;
                    border-radius: 100%;
                    position: relative;
                    color: $copy-right;
                    border: 1px solid $copy-right;
                    
                    .fa {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                    
                    &:hover {
                        color: $social-hover;
                        border-color: $social-hover;
                    }
                }
            }
        }    
    }
    
    &_right_app {
        
        &_apple {
            background-color: $background-apple;
            background: url(/images/footer/pattern-btn.png) repeat-x;
            text-transform: uppercase;
            color: $white;
            padding: 10px 10px 10px 30px;
            border-radius: 20px;
            position: relative;
            font-size: 12px;
            margin-right: 10px;
            
            &:before {
                content: '';
                position: absolute;
                background: url('/images/footer/apple-before.png')no-repeat;
                width: 13px;
                height: 17px;
                left: 10px;
                top: 8px;
            }
            
            &:hover {
                color: $white;
            }
        }        
        &_android {
            background-color: $background-android;
            background: url(/images/footer/pattern-btn.png) repeat-x;
            text-transform: uppercase;
            color: $white;
            padding: 10px 10px 10px 30px;
            border-radius: 20px;
            position: relative;
            font-size: 12px;
            
            &:before {
                content: '';
                position: absolute;
                background: url('/images/footer/google-before.png')no-repeat;
                width: 14px;
                height: 17px;
                left: 10px;
                top: 8px;
            }
            
            &:hover {
                color: $white;
            }
        }
        
    }
}