.center {
    padding-top: 25px;
    &_title {
        font-size: 36px;
        color: $black;
        font-weight: 700;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    
    &_content {
        font-size: 18px;
        
        span {
            font-size: 18px;
            font-weight: 700;
        }
        
        &_ul {
            list-style: disc;
            padding-left: 25px;
            color: $color-links-news;
            
            li {
                margin-bottom: 5px;
                
                &:last-child {
                    margin-bottom: 0;
                }
                span {
                    color: $black;
                    font-weight: 400;
                }
                
            }
        }
        
        &_images {
            margin: 20px 0 40px;
        }
        
        &_flex-images {
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            flex-flow: row wrap;
            margin-bottom: 35px;
            
            img {
                min-height: 160px;
            }
        }
        
        &_share {
            list-style: none;
            text-transform: uppercase;
            font-weight: 700;
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin-top: 15px;
            
            li {
                margin-right: 17px;
                color: $color-share;
                
                a {
                    color: $color-share;
                    width: 40px;
                    height: 40px;
                    border: 1px solid $color-share;
                    border-radius: 100%;
                    position: relative;
                    transition: all .4s;
                    .fa {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%);
                    }
                    
                    &:hover {
                        background-color: $color-share-hover;
                        color: $white;
                        border-color: $color-share-hover;
                    }
                }
            }
        }
        
//        Page visitors
        
        &_banner {
            margin: 30px 0 60px;    
        }
    }
}

//Page catalog

.catalog-item {
    margin-bottom: 20px;
    
    &_wrapper-catalog {
        min-height: 195px;
        position: relative;
        margin-bottom: 10px;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    
    &_catalog-title {
        font-weight: 700;
        margin-bottom: 0px;
        text-transform: uppercase;
        line-height: 1;
    }
    
    &_catalog-download {
        text-transform: uppercase;
        font-size: 14px;
        border-bottom: 2px solid #4a63a8;
        color: #4a63a8;
    }
}

//Page contact

.contact-page {
    margin-top: 50px;
    .contact_list-contact {
        li {
            color: #525252;
            font-size: 20px;
            
            a {
                color: $black;
                font-size: 26px;
            }
        }
    }
    
    .contact_wrapper-avatar {
        max-width: 270px;
    }
    
    .contact_list-contact {
        padding-left: 285px;
    }
    
    .contact_list-contact li:first-child {
        font-size: 30px;
        color: $black;
    }
    
    .contact_list-contact_links {
        margin-top: 35px;
    }
    
    .contact-page_location p {
        text-transform: uppercase;
        margin-bottom: 10px;
        
        &:last-child {
            margin-bottom: 0;
        }
    }
    
    .contact-page_location .contact-page_location-title {
        font-size: 30px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 10px;
    }
    
    .wall-car {
        margin-top: 50px;
        
        p {
            text-transform: none;
        }
        
        .contact-page_location-title {
            margin-bottom: 10px;
        }
    }
    
}

//Page news

.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover {
    font-size: 20px;
    font-weight: 700;
    display: inline-block;
    padding: 0 25px 5px;
    border: none;
    border-bottom: 2px solid #ffb400;
    color: $black;
}

.nav>li>a {
    padding: 0 15px;
    font-size: 20px;
    color: #858585;
    font-weight: 700;
}

.nav-tabs {
    margin-bottom: 30px;
    border-bottom: 2px solid #ddd;
}


