/*==========================================================
                        NORMALIZE
==========================================================*/
img{
	max-width: 100%;
	width: 100%;
}
ul{
	list-style: none;
    padding-left: 0;
}
a {
	text-decoration: none;
	display: inline-block;
    transition: .4s;
}

a:hover {
	text-decoration: none;
}

a:focus {
    outline: none;
}

.bootstrap-select .dropdown-toggle:focus {
     outline: none!important; 
     outline: 0px auto -webkit-focus-ring-color!important; 
     outline-offset: 0px; 
}
