

.square {
    position: absolute;
    width: 70px;
    height: 100%;
    background-color: $back-menu-before;
    transition: all .4s;
    -webkit-box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
    -moz-box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
    box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
    
    &:before {
        content: '';
        position: absolute;
        background: url('/images/main/menu-item.png')no-repeat;
        width: 18px;
        height: 18px;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);

    }
    
    &_conference {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item1.png')no-repeat;
            width: 25px;
            height: 18px;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);

        }        
    }
    &_participants {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item2.png')no-repeat;
            width: 20px;
            height: 20px;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);

        }        
    }
    &_programm {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item3.png')no-repeat;
            width: 20px;
            height: 20px;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);

        }        
    }
    &_photo {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item4.png')no-repeat;
            width: 22px;
            height: 20px;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);

        }        
    }
    &_advertising {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item5.png')no-repeat;
            width: 20px;
            height: 20px;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);

        }        
    }
    &_news {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item6.png')no-repeat;
            width: 23px;
            height: 20px;
            left: 50%;
