            transform: translate(-50%,-50%);

        }        
    }
    &_contacts {

        &:before {
            content: '';
            position: absolute;
            background: url('/images/main/menu-item7.png')no-repeat;
            width: 25px;
            height: 20px;
            left: 50%;
