.nested {
    display: none;
    li {
        &:hover {
            background: $back-menu-hover;

            .square {
                background: $back-menu-before-hover;
            }
        }         
    }
}
.selection_menu .hover-none {
        &:hover {
            background: transparent;

            .square {
                background: $back-menu-before-hover;
            }
        }      
}


