.title {
    font-size: 24px;
    font-weight: 700;
    position: relative;
    padding-left: 25px;
    margin-bottom: 25px;
    text-transform: uppercase;
    
    &:before {
        position: absolute;
        content: '';
        max-width: 13px;
        width: 100%;
        height: 100%;
        background: $back-title;
        left: 0;
    }
}

.date {
    font-weight: 700;
    font-size: 18px;
    padding-left: 20px;
    position: relative;

    &:before {
        content:'';
        position: absolute;
        background: url('/images/main/clock.png')no-repeat;
        width: 14px;
        height: 14px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
    }
}

//Pagination 

.pagination>li>a, .pagination>li>span {
    border:1px solid transparent;
    transition: all .4s;
    color: $black;
    font-weight: 700;
    font-size: 24px;
    line-height: 1;
}

.pagination>li>a:focus,
.pagination>li>span:focus {
    background-color: $color-links-news;
    border-radius: 2px;
    color: $white;
}
.pagination>.active>a,
.pagination>.active>a:focus,
.pagination>.active>span:focus {
    background-color: $color-links-news;
    border-radius: 2px;
    color: $white;
}
.pagination>li>a:hover, 
.pagination>li>span:hover {
    border-radius: 2px;
    background-color: transparent;
    border: 1px solid $color-links-news;
    color: $black;  
}

