//Flex container
.flex-container  {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-flow: row wrap;
}

//Btn registration

.btn-registration {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#efd600+1,e4af01+100 */
    background: #efd600; /* Old browsers */
    background: -moz-linear-gradient(top,  #efd600 1%, #e4af01 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  #efd600 1%,#e4af01 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  #efd600 1%,#e4af01 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#efd600', endColorstr='#e4af01',GradientType=0 ); /* IE6-9 */
    border-bottom: 4px solid #bc8e00;
    
    text-transform: uppercase;
    color: $black;
    font-weight: 700;
    font-size: 24px;
    max-width: 225px;
    width: 100%;
    text-align: center;
    border-radius: 4px;
    padding: 35px 0;
    margin-top: 35px;
    
    &:hover {
        background: #bc8e00;
        color: #fff;
    }

}

