//Pagination next-prev open news

.pager {
    padding: 30px 0;
    border-top: 1px solid $color-share;
    border-bottom: 1px solid $color-share;
}
.pager .previous a {
    position: relative;
    border: none;
    padding-left: 50px;
    color: $black;
    transition: all .4s;
}
.pager .previous a:hover {
    background-color: transparent;
}
.pager .previous a:hover:before {
    background-color: $color-share-hover;
    background: url('/images/main/arrow-prev-hover.png')no-repeat center;
}
.pager .previous a:before {
    content: '';
    position: absolute;
    width: 38px;
    height: 38px;
    -webkit-transition: all .4s;
    transition: all .4s;
    background: url(/images/main/arrow-prev.png) no-repeat center;
    border: 1px solid $color-share;
    left: 0;
    top: 0;
    border-radius: 100%;
}
.pager .next a {
    position: relative;
    border: none;
    padding-right: 50px;
    color: $black;
    transition: all .4s;
}
.pager .next a:hover {
    background-color: transparent;
}
.pager .next a:hover:before {
    background-color: $color-share-hover;
    background: url(/images/main/arrow-next-hover.png) no-repeat center;
}
.pager .next a:before {
    content: '';
    position: absolute;
    width: 38px;
    height: 38px;
    -webkit-transition: all .4s;
    transition: all .4s;
    background: url(/images/main/arrow-next.png) no-repeat center;
    border: 1px solid $color-share;
    right: 0;
    top: 0;
    border-radius: 100%;
}

//Pop up images


.popup {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: none;
    text-align: center;
}

.popup_bg {
    background:rgba(0,0,0,0.4);
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 100%;
}

.popup_img {
    position: relative;
    margin: 0 auto;
    z-index: 2;
    max-height: 94%;
    max-width: 94%;
    margin: 1% 0 0 0;
}

