.advertising {
    &_wrapper-images {
        margin-bottom: 20px;
        position: relative;
        .size {
            position: absolute;
            left: 50%;
            bottom: -10px;
            transform: translateX(-50%);
            padding: 10px 15px;
            text-transform: uppercase;
            border-radius: 4px;
            background-color: #f8d593;
        }
    }
    
    &_price {
        font-size: 60px;
        font-weight: 700;
        line-height: 1;
        color: $black;
    }
    &_period {
        color: #a5a5a5 ;
        text-transform: uppercase;
        font-size: 18px;
        display: inline-block;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            background-color: #a5a5a5 ;
            width: 5px;
            height: 1px;
            left: -10px;
            top: 50%;
            transform: translate(-50%);
        }
        &:after {
            content: '';
            position: absolute;
            background-color: #a5a5a5 ;
            width: 5px;
            height: 1px;
            right: -10px;
            top: 50%;
            transform: translate(-50%);
        }
    }
    
    &_text {
        font-size: 20px;
        font-weight: 700;
    }
    
    &_list-price {
        font-size: 20px;
        margin-bottom: 40px;
        text-transform: uppercase;
    }
}

.line-pluse {
    width: 100%;
    height: 1px;
    background-color: $color-border;
    position: relative;
    margin: 55px 0;
    
    &:before {
        content: '';
        position: absolute;
        top: -25px;
        left: 50%;
        width: 50px;
        height: 50px;
        border: 1px solid #e8e8e8;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #fff url('/images/main/plus.png')no-repeat center;
        
    }
}

.write-articles {
    .advertising_price {
        font-size: 48px;
    }
}

.articles {
    p {
        font-size: 18px;
        padding: 5px 0;
        text-align: center;
        background-color: #f8d593;    
        margin: 90px 0 35px;
    }
    
    &_list {
        list-style: disc;
        padding-left: 20px;
        font-size: 22px;
        li {
            color: $back-btn;
            &:first-child {
                font-weight: 700;
                margin-bottom: 30px;
                list-style: none;
                margin-left: -20px;
                color: $black;
            }
            
            span {
                color: $black;
                font-size: 18px;
            }
            
        }
    }

}

.dashed {
    border-right: 1px dashed $color-border
}