.photo {
    padding-top: 25px;
    &_wrapper-images {
        position: relative;
        margin-bottom: 30px;
        &_album {
            text-align: left;
            position: absolute;
            bottom: 0;
            width: 100%;
            background-color: rgba(0,0,0, .6);
            color: $white;
            padding: 15px 0 15px 30px;
            text-transform: uppercase;
            
            p {
                margin: 0;
                font-size: 14px;
                
                &:first-child {
                    font-weight: 700;
                    font-size: 18px;
                }
                
            }
        }
    }
    
}