.buy-ticket {
    background-color: $background-section;
    padding: 30px 0;
    
// Date block
    &_date {
        color: $black;
        text-transform: capitalize;
        float: left;
        
        max-width: 100px;
        width: 100%;
        background-color: $white;
        text-align: center;
        border-radius: 4px;
        
        li {
            padding: 10px 0;
            font-size: 38px;
            &:first-child {
                background-color: $background-date;
                border-radius: 4px;
                font-size: 18px;
            }
        }
    }
    
//Location
    &_location {
        color: $white;
        padding-left: 115px;
        font-size: 22px;
        
        &_city {
            font-size: 30px;
            padding-left: 20px;
            position: relative;
            
            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                background: url('/images/main/pin.png')no-repeat;
                width: 12px;
                height: 16px;
            }
        }
        
        li {
            a {
                color: $link;
                font-size: 19px;
                border-bottom: 1px dashed $link;
            }
        }
    }
    
//Buy ticket btn
    
    &_btn {
        color: $white;
        text-transform: uppercase;
        font-size: 32px;
        width: 100%;
        padding:40px 0;
        text-align: center;
        border-radius: 5px;
        font-weight: 700;
    }
    
    &_buy {
        background-color: $back-btn;
        background: url('/images/main/back-buy.png')no-repeat;
        background-size: cover;
        border-bottom: 5px solid $back-btn;
        
        &:hover {
            background: #0035ab;
            color: #fff;
            border-bottom-color: #0035ab;
        }
    }
    
    &_advertising {
        background-color: $back-adv;
        background: url('/images/main/back-adv.png')no-repeat;
        background-size: cover;   
        border-bottom: 5px solid  $back-adv;
        
        &:hover {
            background: #4a8906;
            color: #fff;
            border-bottom-color: #4a8906;            
        }
    }
    
}