.header {
    font:400 18px $Roboto;
    background-color: $white;
    line-height: 85px;
//    Logotipe
///////////////////////////
    &_logotipe {
        max-width: 110px;
        float: left;
    }
//End logotipe
    
//Nav
////////////////////////////
    &_nav {
        float: right;
        margin-right: 68px;
        
        &_list {
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            margin-bottom: 0;
            li {
                margin-right: 20px;

                &:last-child {
                    margin-right: 0;
                }

                a {
                    color: $black;
                    border-bottom: 4px solid transparent;

                    &:hover {
                        border-bottom: 4px solid $border-color;
                    }
                }

            }            
        }
    }
//End nav

//Language
/////////////////////////////
    
    &_language {
        float: right;
        border: none;
        position: relative;
        
        &:before {
            content: '';
            position: absolute;
            background: url('/images/header/globe.png')no-repeat;
            width: 15px;
            height: 15px;
            left: -10px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    
    .btn-default {
        border: none;
    }
    
    .dropdown-menu {
        min-width: 100px;
        background-color: #e8e8e8;
        top: 70%;
    }
    
    .dropdown-menu>li>a:focus,
    .dropdown-menu>li>a:hover {
        background-color: #d7d7d7;
    }
    
    .btn-default.active.focus,
    .btn-default.active:focus,
    .btn-default.active:hover,
    .btn-default:active.focus,
    .btn-default:active:focus,
    .btn-default:active:hover,
    .open>.dropdown-toggle.btn-default.focus,
    .open>.dropdown-toggle.btn-default:focus,
    .open>.dropdown-toggle.btn-default:hover {
        background-color: transparent;
    }
    
    .btn-default:hover {
        background-color: transparent;
    }
    
    .btn {
        line-height: 1;
    }
//End lendguage    
    
    .hamburger {
        display: none;
    }
}


