.partner-sponsor {
    padding: 65px 0;
    
    .wrapper-title {
        width: 100%;
        
        border-bottom: 2px solid $color-border;
        margin-bottom: 60px;
        margin-top: 10px;
        
        .small-title {
            display: inline-block;
            padding: 0 25px 5px;
            border-bottom: 2px solid $color-border-small-title;
            font-size: 20px;
            font-weight: 700;
        }
    }
    
    &_list {
        width: 100%;
        &_wrapper-images {
            border-radius: 0;
            border: 1px solid $color-border-list;
            margin-bottom: 20px;
        }
    }
    
    .slick-prev {
        background: url('/images/main/prev.png')no-repeat;
        background-size: contain;
        width: 38px;
        height: 38px;
        transition: all .4s;
        left: -5px;
        z-index: 5;
        &:hover {
            background: url('/images/main/prev-hover.png')no-repeat;
            width: 38px;
            height: 38px;
            background-size: contain;
        }
    } 
    .slick-next  {
        background: url('/images/main/next.png')no-repeat;
        background-size: contain;
        width: 38px;
        height: 38px;
        transition: all .4s;
        right: -5px;
        z-index: 5;
        &:hover {
            background: url('/images/main/next-hover.png')no-repeat;
            background-size: contain;
            width: 38px;
            height: 38px;
        }
    }  
    
    .slick-prev:before,
    .slick-next:before {
        display: none;
    }
    
}

.button-xs {
    display: none;
}

.slick-initialized .slick-slide div {
    margin: 0 5px 0px 5px;
}