@media (max-width: 767px) {
    .header_language {
        margin-right: 0;
    }
    
    .footer {
        text-align: center;
        padding-bottom: 35px;
    }
    .footer_left_social_social {
        justify-content: center;
    }
    
    .footer_left_social {
        float: none;
    }
    .footer .footer_right_app {
        float: none;
    }
    
//    Section buy ticket 
    .buy-ticket_btn {
        padding: 20px 0;
        font-size: 16px;
    }
    
    .flex-container-xs {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        width: 100%;
        flex-flow: row nowrap;
    }
    
    .buy-ticket_buy {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }
    
//    Section selected
    .padding-right {
        padding-right: 15px;
    }
    .padding-left {
        padding-left: 15px;
    }  
    
    
//    Col-xss-12
    
    .col-xss-12 {
        max-width: 320px;
        width: 100%;
        padding: 0 15px;
        margin: auto;
    }
    
    .contact .flex-container {
        flex-flow: column;
    }
    .contact .flex-container:before {
        display: none;
    }
    .contact .flex-container .contact_margin {
        margin-bottom: 30px;
    }
    .contact .flex-container .contact_margin:last-child {
        margin-bottom: 00px;
    }
    
    
    
    .content_news-right_block_content p {
        display: none;
    }
    .content_news-right_block_content a {
        font-size: 15px;
    }
    
    .content_news-right_block_images {
        max-width: 90px;
    }
    
    .content_news-right_block_content {
        padding-left: 100px;
    }
    
    .flex-form {
        flex-flow: row wrap;
    }
    
    .address {
        max-width: 290px;
        left: 50%;
        transform: translateX(-50%);
    }
    
    .button-xs {
        display: inline-block;
        text-align: center;
        width: 100%;
        margin-top: 20px;
    }
    .partner-sponsor .btn-sub {
        max-width: 215px;
        width: 100%;
        margin: auto;
    }
    
    .center_content_share {
        justify-content: flex-start;
    }
    
    .margin-xs-images {
        margin-bottom: 10px;
    }
    
    .btn-registration {
        font-size: 15px;
    }
    
    .contact-page .contact_wrapper-avatar {
        float: none;
        margin:0 0 10px;
    }
    
    .contact-page .contact_list-contact {
        padding-left: 0;
    }
    
    .contact-page .contact_list-contact li {
        margin-bottom: 10px;
    }
    
    .contact-page .contact-page_location .contact-page_location-title {
        font-size: 24px;
        margin-bottom: 10px;
    }
    
    
    .nav > li > a {
        font-size: 15px;
        padding: 0 5px;
    }
    
    .nav-tabs > li.active > a, .nav-tabs > li.active > a:focus, .nav-tabs > li.active > a:hover {
        padding: 0 5px;
        font-size: 15px;
    }
    
    .pager  a {
        font-size: 15px;
    }
    .pager .previous a {
        padding: 5px 0 5px 40px;
    }
    .pager .next a {
        padding: 5px 40px 5px 0px;
    }
    .pager .previous a:before,
    .pager .next a:before {
        width: 35px;
        height: 35px;
    }
    
    .partner-sponsor_list_wrapper-images {
        max-width: 180px;
        width: 100%;
        margin: auto;
    }
    .slick-slider {
        text-align: center;
    }
    
    .content_program_start {
        padding: 30px 15px 30px 0;
    }
    .content_program_date .day {
        padding: 10px 20px;
    }
    .content_program_date p {
        font-size: 36px;
    }
    .content_program_time p {
        font-size: 25px;
    }
    
    .content_program_speaker_info .presentation {
        font-size: 13px;
    }
    .content_program_speaker_info .biography {
        font-size: 13px;
    }
    .dashed {
        border: transparent;
    }
    
    .write-articles .slick-prev {
        background: url('/images/main/arrow-art1.png')no-repeat center;
        width: 12px;
        height: 20px;
        left: 0px;
        z-index: 2;
        
    }    
    .write-articles .slick-next {
        background: url('/images/main/arrow-art.png')no-repeat center;
        width: 12px;
        height: 20px;
        right: 0px;
        z-index: 2;
        
    }
    .write-articles .slick-prev:before, 
    .write-articles .slick-next:before, {
        display: none;
    }
    
    .speaker-page .content_program_speaker_avatar {
        float: none;
    }    
    .speaker-page .content_program_speaker_info {
        padding-left: 0;
    }
    
    .footer-page {
        margin-top: -800px;
        padding-top: 820px;
    }
    
    .pull-right {
        float: none !important;
    }
    
    .breadcrumb {
        margin-top: -20px;
        padding-left: 20px;
    }
    
    .hide-menu-xs {
        display: none;
    }
    
    .center {
        padding-top: 0;
    }
    
    
}