//Style form demozone

.form {
    padding: 40px 30px;
    background-color: $back-form-right;
    
    .form-group {
        
        .form-control {
            height: 50px;
            border: none;
        }
        
        .captcha {
            max-width: 304px;
            width: 100%;
            margin: 15px 0;
        }      
        
    }
    
    .submit {
        border: none;
        font-size: 20px;
        font-weight: 700;
        width: 100%;
        max-width: 370px;
        margin-top: 35px;
        padding: 25px 0;
        text-align: center;
        text-transform: uppercase;
        color: #000;
        border-radius: 4px;
        background: #efd600;
        background: -webkit-linear-gradient(top, #efd600 1%, #e4af01 100%);
        background: linear-gradient(to bottom, #efd600 1%, #e4af01 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#efd600', endColorstr='#e4af01',GradientType=0 );
        border-bottom: 4px solid #bc8e00;
    }
    .has-error .form-control {
        border: 1px solid red;
    }    
    .has-error .halp-block {
        color: red;
        font-size: 18px;
    }
    
    .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width: 100%;
        height: 50px;
    }
    .bootstrap-select>.dropdown-toggle {
        height: 100%;
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#efd400+1,e5b001+100 */
        background: #efd400; /* Old browsers */
        background: -moz-linear-gradient(top,  #efd400 1%, #e5b001 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  #efd400 1%,#e5b001 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  #efd400 1%,#e5b001 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#efd400', endColorstr='#e5b001',GradientType=0 ); /* IE6-9 */

        border: 1px solid #eecd00;
    }
    
    .btn-default.active.focus, .btn-default.active:focus, .btn-default.active:hover, .btn-default:active.focus, .btn-default:active:focus, .btn-default:active:hover, .open>.dropdown-toggle.btn-default.focus, .open>.dropdown-toggle.btn-default:focus, .open>.dropdown-toggle.btn-default:hover {
        background-color: #eecd00;
        color: #454545;
        outline: none;
    }
    .dropdown-menu>li>a:focus, .dropdown-menu>li>a {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 700;
    }
    .dropdown-menu>li>a:focus, .dropdown-menu>li>a:hover {
        background-color: #eecd00;
        color: $white;    
    }
    .bootstrap-select.btn-group .dropdown-toggle .filter-option {
        font-weight: 700;
        font-size: 18px;
        text-transform: uppercase;
    }
    
    .bootstrap-select.btn-group .open {
        border: 1px solid #eecd00;
    }
    
::-webkit-input-placeholder {
    font-size:18px;
    }
::-moz-placeholder          {
    font-size:18px;
    }/* Firefox 19+ */
:-moz-placeholder           {
    font-size:18px;
    }/* Firefox 18- */
:-ms-input-placeholder      {
    font-size:18px;
    }    
    
}



//End Style form demozone