.content {
    &_program {
        
        tr {
            border-top: 1px solid $color-border;
            border-bottom: 1px solid $color-border;    
        }
        
        &_date {
            -webkit-box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8);
            -moz-box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8);
            box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8);
            border-radius: 4px;
            p {
                font-size: 38px;
                text-align: center;
                font-weight: 700;
                position: relative;
                z-index: 1;
                line-height: 1;
                padding: 5px 0 10px;
            }
            .day {
                font-size: 18px;
                background-color: #f8d593;
                padding: 10px 25px;
                border-top-right-radius: 4px;
                border-top-left-radius: 4px;
                margin-bottom: 0;
            }
        }
        
        &_time {
            -webkit-box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8);
            -moz-box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8);
            box-shadow: 0 0 10px -3px rgba(0, 0, 0, 0.8); 
            border-radius: 4px;
            padding:10px 5px;
            position: relative;
            min-width: 100px;
            
            &:before {
                content: '';
                position: absolute;
                width: 1px;
                height: 100%;
                background-color: $color-border;
                left: 50%;
                top: 0;
                transform: translateX(-50%);
                z-index: -1;
            }  
            
            p {
                font-size: 30px;
                font-weight: 700;
                text-align: center;
                margin-bottom: 0;
                line-height: 1;
                
            }
        }
        
        &_start {
            padding: 30px 20px 30px 0;
            border-right: 1px solid $color-border;
        }
        
        &_speaker {
            padding: 30px 25px;
            
            &_avatar {
                float: left;
                .wrapper-avatar {
                    max-width: 180px;
                }
                .social {
                    margin: 10px 0;
                    li {
                        float: left;
                        margin-right: 10px;
                        &:last-child {
                            margin-right: 0;
                        }
                        
                        a {
                            width: 23px;
                            height: 23px;
                            position: relative;
                            border: 1px solid inherit;
                            
                            .fa {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%,-50%);
                                color: $white;
                            }
                            
                        }
                        .facebook {
                            background: #006eb4;
                            border: 1px solid #006eb4;
                        }
                        .vk {
                            background: #4a719d;
                            border: 1px solid #4a719d;
                        }
                        .inst {
                            background: #8d6c58;
                            border: 1px solid #8d6c58;
                        }
                        .twitter {
                            background: #00b5ed;
                            border: 1px solid #00b5ed;
                        }
                    }
                }
            }
            
            &_info {
                padding-left: 200px;
                
                .name {
                    font-size: 48px;
                    font-weight: 700;
                    line-height: 1;
                }
                .professional {
                    font-size: 28px;
                    line-height: 1;
                    color: #747474;
                    margin-bottom: 10px;
                }
                .biography {
                    color: #064bbe;
                    font-size: 25px;
                    text-transform: capitalize;
                    text-decoration: underline;
                    margin-right: 30px;
                    position: relative;
                    margin-bottom: 25px;
                    &:before {
                        content: '';
                        position: absolute;
                        background-color:$color-border;
                        width: 1px;
                        height: 100%;
                        right: -15px;
                    }
                }
                .presentation {
                    color: #910011;
                    font-size: 25px;
                    text-transform: capitalize;
                    text-decoration: underline;
                }
                
                li {
                    font-size: 25px;
                    line-height: 1;
                }
            }
        }        
            
    }

}

.margin-program {
    margin-top: 85px;
}