.selection {
    margin-top: -90px;
   
    &_menu {
        text-align: center;
        background: $back-menu;
        margin-bottom: 25px;
        margin-top: -95px;
        li {
            position: relative;
            transition: all .4s;
            
            a {
                color: $white;
                font-size: 26px;
                position: relative;
                width: 100%;
                padding: 25px 0;
                -webkit-box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
                -moz-box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
                box-shadow: inset 0px -191px 55px -195px rgba(0,0,0,0.46);
                padding-left: 100px;
                text-align: left;
                
                &:hover {
                    box-shadow: none;                    
                }
            }
            
            .nested {
                
                a {
                    position: relative;
                    text-transform: none;
                    &:before {
                        content: '';
                        position: absolute;
                        left: 80px;
                        top: 50%;
                        width: 0;
                        height: 0;
                        border-top: 5px solid transparent;
                        border-left: 7px solid $white;;
                        border-bottom: 5px solid transparent;
                        transform: translateY(-50%);
                        
                    }
                }
                
            }
            .open-menu {
                width: 42px;
                height: 42px;
                padding: 0;
                position: absolute;
                text-align: 0;
                top: 50%;
                right: 15px;
                transform: translateY(-50%);
                background: url('/images/main/arrow-nav.png')no-repeat;
                background-size: contain;
                cursor: pointer;   
            }
            .open-mobile-menu {
                width: 16px;
                height: 10px;
                padding: 0;
                position: absolute;
                text-align: 0;
                top: 50%;
                right: 15px;
                transform: translateY(-50%);
                background: url('/images/main/arrow-nav-mobile.png')no-repeat;
                background-size: contain;
                cursor: pointer;
                box-shadow: none;
            }
            .top {
                width: 42px;
                height: 42px;
                background: url('/images/main/arrow-nav-top.png')no-repeat;
                background-size: contain;
            } 
            .mobile-top {
                width: 16px;
                height: 10px;
                background: url('/images/main/arrow-nav-mobile-top.png')no-repeat;
                background-size: contain;
            }             
            
            &:hover {
                background: $back-menu-hover;
                .square {
                    background: $back-menu-before-hover;
                }
            }
            
        }
        
        &_contact {
            background: #274293;
        }
     
    }
    
//Right block 
    
//    block speaker
    &_left { 
        background-color: $back-speaker;
        padding: 40px 20px 40px 0;
        background: url('/images/main/back-left.png')no-repeat;        
        &_content {
            color: $white;
            text-transform: uppercase;
            max-width: 170px;
            width: 100%;
            float: right;
            font-size: 10px;
            position: relative;
            z-index: 2;

            &_title {
                font-size: 25px;
                font-weight: 700;
                line-height: 1;
                p {
                    margin: 0;
                }
            }
            
            &_count {
                font-size: 38px;
            }            

        }
    }    
//    block sponsor
    &_right { 
        background-color: $back-speaker;
        background: url('/images/main/back-right.png')no-repeat;
        padding: 40px 0px 40px 20px;
        &_content {
            color: $white;
            text-transform: uppercase;
            max-width: 170px;
            width: 100%;
            float: left;
            font-size: 10px;
            position: relative;
            z-index: 2;

            &_title {
                font-size: 25px;
                font-weight: 700;
                line-height: 1;
                
                p {
                    margin: 0;
                }
            }
            
            &_count {
                font-size: 38px;
            }            

        }
    }
}

.selection .selection_menu {
    margin-top: 0;
}

