@media (max-width: 991px) {
//    Header
    
    
    .header {
        position: relative;
        z-index: 1041;
        border-bottom: 1px solid $border-header;
        line-height: 1;   
        padding: 15px 0 10px;
    }
    .header_nav_list {
        flex-flow: column;
        line-height: 2;
        text-align: center;  
        width: 100%;
        
    }
    .header_nav_list li {
        margin-right: 0;
    }
    .header_nav_list li a {
        border: none;
        text-transform: uppercase;
    }
    .header_nav_list li a:hover {
        border-bottom:none;
        color: $border-color;        
    }
    
    .header .hamburger {
        display: block;
        float: right;
        position: relative;
        
    }
    
    .header_nav {
        width: 100%;
        position: absolute;
        float: none;
        left: 0;
        top: 116px;
        background-color: $white;
        display: none;
        
    }
    
    .header .hamburger:focus {
        outline: none;
    }
    .menu-active {
        display: block;
        z-index: 1041;
    }
    
    .header_language {
        margin-right: 30px;
        padding: 15px 0;
    }
    
//    Section buy ticket
    
    .buy-ticket_date {
        max-width: 80px;
    }
    
    .buy-ticket_location {
        padding-left: 90px;
        font-size: 16px;
    }
    .buy-ticket_location_city {
        font-size: 16px;
    }
    
    .buy-ticket_location li a {
        font-size: 12px;
    }
    
    .buy-ticket_date li {
        padding: 10px 0;
        font-size: 30px;
    }
    
    .buy-ticket_btn {
        font-size: 20px;
    }
    
//    Section selected
    .sponsor:before {
        bottom: 0;
        width: 95px;
        height: 110px;
        background-size: cover;        
    }
    .stend:before,
    .speaker:before {
        width: 130px;
        height: 100px;
        background-size: contain;
        bottom: 0;
    }
    .ticket:before {
        width: 100px;
        background-size: contain;
        bottom: 0;
        height: 90px;
    }
    .selection_left_content_title {
        font-size: 19px;
    }
    
    .selection_left_content_count {
        font-size: 22px;
        margin: 0;
        line-height: 1;
    }
    .selection_left_content {
        max-width: 115px;
    }
    
    .selection_left {
        padding: 20px 10px 20px 0;
    }
    .selection_right_content_title {
        font-size: 19px;
    }
    
    .selection_right_content_count {
        font-size: 22px;
        margin: 0;
        line-height: 1;
    }
    .selection_right_content {
        max-width: 115px;
    }
    
    .selection_right {
        padding: 20px 0px 20px 20px;
    }
    .selection_menu li a {
        padding: 20px 0 20px 60px ;
        font-size: 18px;
    }
    
    .square {
        width: 50px;
    }
    
    // Form-left
    .content_form {
        padding: 20px 15px 30px;    
    }
    .content_form .form-control {
        height: 40px;
    }
    
    .title {
        font-size: 18px;
    }
    
    .content_news-right_block_content a {
        font-size: 20px;
    }
    .content_news-right_block_content p {
        font-size: 15px;
    }
    
    .content_news-left_block_images {
        max-width: 90px;
    }
    .content_news-left_block_content {
        padding-left: 100px;
    }
    
    .content_news-left_block_content a {
        font-size: 16px;
    }
    .date {
        font-size: 16px;
    }
    
    .pagination > li > a, .pagination > li > span {
        font-size: 20px;
    }
    .content_form-right_title {
        margin-bottom: 20px;
    }
    
    .btn-sub {
        font-size: 14px;
    }
    
//    Addres
    .address_list_date li:first-child {
        font-size: 18px;
    }
    
    .address_list_location li:last-child {
        font-size: 18px;
    }
    
    .address_list_date li:last-child {
        font-size: 18px;
    }
    
    .contact-page .contact_list-contact_links li:first-child a {
        font-size: 18px;
    }
    .contact-page .contact_list-contact li a {
        font-size: 18px;
    }
    
    .contact-page .contact_wrapper-avatar {
        max-width: 180px;
    }
    .contact-page .contact_list-contact {
        padding-left: 195px;
    }
    
    .contact-page .contact_list-contact li:first-child {
        font-size: 20px;
    }
    
    .contact-page .contact_list-contact li {
        font-size: 16px;
    }
    
    .contact-page .contact_list-contact_links {
        margin-top: 25px;
    }
    
    .content_program_speaker_info .name {
        font-size: 22px;
    }
    
    .content_program_speaker_info .professional {
        font-size: 15px;
    }
    
    .content_program_speaker_info .biography {
        font-size: 15px;
        margin-right: 10px;
        margin-bottom: 20px;
    }
    .content_program_speaker_info .biography:before {
        right: -8px;
    }
    
    .content_program_speaker_info .presentation {
        font-size: 15px;
    }
    
    .content_program_speaker {
        padding: 20px 0px 20px 15px
    }
    
    .content_program_speaker_avatar .wrapper-avatar {
        max-width: 125px;
    }
    .content_program_speaker_info {
        padding-left: 135px;
    }
    .content_program_speaker_info li {
        font-size: 15px;
    }
    
    .content_program_speaker_avatar .wrapper-avatar {
        float: none;
    }
    
    .content_program_speaker_avatar {
        float: none;
    }
    
    .content_program_speaker_info {
        padding-left: 0;
    }
    
    .speaker-page .content_program_speaker_avatar {
        float: left;
    }    
    .speaker-page .content_program_speaker_info {
        padding-left: 135px;
    }
    
    .photo_wrapper-images_album p:first-child {
        font-size: 12px;
    }
    .photo_wrapper-images_album p {
        font-size: 12px;
    }
    
    .center_content {
        margin-bottom: 20px;
    }
}