.map {
    position: relative;
}
.address {
    position: absolute;
    top: 10%;
    left: 26%;
    padding: 45px 0 90px;
    background-color: #000;
    max-width: 450px;
    width: 100%;
    
    .title {
        color: $white;
        margin-bottom: 30px;
    }
    
    &_list {
        padding-left: 25px;
        
        &_date {
            position: relative;
            
            &:before {
                content: '';
                position: absolute;
                background: url('/images/main/calendar.png')no-repeat;
                width: 26px;
                height: 26px;
                left: 0;
            }
            
            li {
                &:first-child {
                    font-size: 20px;
                    text-transform: uppercase;
                    font-weight: 700;
                    color: $date-color;  
                    padding-left: 40px;
                }
                
                &:last-child {
                    text-transform: uppercase;
                    color: #fff;
                    font-size: 24px;
                    font-weight: 700;
                    padding-left: 40px;
                }
            }
        }
        
        &_location {
            margin-top: 25px;
            padding-left: 40px;
            position: relative;
            
            &:before {
                content: '';
                position: absolute;
                background: url('/images/main/market.png')no-repeat;
                width: 26px;
                height: 33px;
                left: 0;
            }
            
            li {
                &:first-child {
                    font-size: 20px;
                    text-transform: uppercase;
                    font-weight: 700;
                    color: $date-color;                      
                }
                &:last-child {
                    text-transform: uppercase;
                    color: #fff;
                    font-size: 24px;
                    font-weight: 400;
                }                
            }
        }
    }
}

.map .container {
    position: relative;
}

.map-box {
    height: 500px;
}