@media (max-width: 1199px) {
    
    
    .selection_menu li a {
        padding-left: 80px;
        font-size: 20px;
    }
    
    .selection_menu li .nested a {
        padding-left: 100px;
    }
    .selection_right_content_title,
    .selection_left_content_title {
        font-size: 22px;
    }
    .selection_right_content_count,
    .selection_left_content_count {
        font-size: 22px;
        margin: 0;
        line-height: 1;
    }
    .selection_right_content,
    .selection_left_content {
        max-width: 130px;
    }
    .speaker:before {
        width: 195px;
        height: 157px;
        background-size: contain;
    }
    .sponsor:before {
        width: 160px;
        height: 215px;
        background-size: contain;
    }
    .stend:before {
        width: 152px;
        height: 148px; 
        background-size: contain;
    }
    .ticket:before {
        width: 175px;
        height: 154px;
        background-size: contain;
    }
    .footer .footer_left_social {
        max-width: 100%;
    }
    
    .footer .footer_right_app {
        float: left;
    }
    
//    Section Buy ticket
    .buy-ticket_date {
        max-width: 95px;
    }
    .buy-ticket_location {
        padding-left: 105px;
        font-size: 18px;
    }
    
    .buy-ticket_location_city {
        font-size: 22px;
    }
    .buy-ticket_location li a {
        font-size: 16px;
    }
    
    .buy-ticket_btn {
        font-size: 25px;
    }
    
//    Map
    .address {
        left: 10%;
    }
    
//    Contact
    .contact_wrapper-avatar {
        max-width: 115px;
    }
    
    .contact_list-contact {
        padding-left: 125px;
    }
    .contact_list-contact_links a {
        font-size: 13px;
    }
    
    .contact_list-contact_links li:first-child a {
        padding-left: 20px;
    }
    
    .contact_list-contact li:first-child {
        font-size: 15px;
    }
    
    .contact_list-contact li {
        font-size: 15px;
    }
    
    .btn-sub {
        font-size: 17px;
    }
    
    .content_program_speaker_info .name {
        font-size: 30px;
    }
    .content_program_speaker_info .biography {
        font-size: 18px;
        margin-top: 10px;
        margin-right: 20px;
    }
    .content_program_speaker_info .presentation {
        font-size: 18px;
    }
    .content_program_speaker_info li {
        font-size: 18px;
    }
    .content_program_speaker_info .professional {
        font-size: 20px;
    }
    
    .write-articles .advertising_price {
        font-size: 44px;
    }
    
    .photo_wrapper-images_album p:first-child {
        font-size: 16px;
    }
    
}