.content {
    .margin0 {
        margin-bottom: 20px;   
    }
//    Left block
////////////////////////////
    &_form {
        background: $back-form;
        padding: 40px 30px 50px;
        margin: 10px 0 40px;
        
        .form-control {
            height: 55px;
            border-radius: 5px;
            position: relative;
            border: transparent;
            
        }
        .btn-sub {
            max-width: 300px;
            width: 100%;
            
        }
        
        
        &_title {
            font-size: 28px;
            font-weight: 700;
            text-transform: uppercase;
            p {
                font-size: 14px;
                font-weight: 400;
            }
        }
        
    }
    &_baner-left {
        margin-bottom: 40px;
    }
    
    &_news-left {
        
        &_block {
            margin-bottom: 35px;
            &_images {
                max-width: 100px;
                float: left;
            }
            
            &_content {
                padding-left: 120px;
                
                a {
                    color: $color-links-news;
                    font-size: 22px;
                    margin-bottom: 15px;
                    line-height: 1;
                    
                    &:hover {
                        text-decoration: underline;                        
                    }
                }
            }
        }
    }
    
//    Right block
//////////////////////
    &_baner-right {
        margin: 10px 0 65px;
    }
    
    &_news-right {
        &_block {
            margin-bottom: 50px;
            &_images {
                max-width: 165px;
                width: 100%;
                float: left;
            }
            
            &_content {
                padding-left: 180px;
                
                a {
                    font-size: 24px;
                    font-weight: 700;
                    color: $color-links-news;
                    line-height: 1;
                }
                p {
                    font-size: 18px;
                }
            }
        }
    }
    
//    Form
    &_form-right {
        background: $back-form-right;
        padding: 30px;
        margin-bottom: 110px;
        
        .wrapper-group {
            max-width: 305px;
            width: 100%;
            margin-right: 10px;
            
            .form-control {
                height: 55px;
                border: transparent;
            }
        }

        .btn-sub {
            float: right;
            margin-top: 20px;
            width: auto;
        }
        
        &_title {
            font-size: 28px;
            font-weight: 700;
            text-transform: uppercase;
            margin-bottom: 40px;
        }
    }
    
    ::-webkit-input-placeholder {
        text-transform: uppercase;
    }
    
    ::-moz-placeholder {
        text-transform: uppercase;
    }
    /* Firefox 19+ */
    
    :-moz-placeholder {
        text-transform: uppercase;
    }
    /* Firefox 18- */
    
    :-ms-input-placeholder {
        text-transform: uppercase;
    }
    
}

.input-group {
    width: 100%;
}
.input-group-addon {
    background-color: $back-span;
    width: 65px;
    position: relative;
    
}

.user {
    
    &:before {
        content: '';
        position: absolute;
        background: url('/images/main/user.png')no-repeat;
        width: 19px;
        height: 18px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
}
.mail {
    
    &:before {
        content: '';
        position: absolute;
        background: url('/images/main/mail.png')no-repeat;
        width: 25px;
        height: 18px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
}